.typingScreen .userInput {
  font-size: 2em;
  margin-bottom: 10px;
}
.typingScreen .soundButton {
  font-size: 1.5em;
}

.typingScreen form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 60%;
  margin-bottom: 20px;
}

.soundControls {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.soundControls > * {
  margin-right: 10px;
}


